import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Group, Div, Avatar, PanelHeader, Counter, Cell } from '@vkontakte/vkui';
import Icon24Like from '@vkontakte/icons/dist/24/like';

const Home = ({ id, go, fetchedUser, user }) => (
	<Panel id={id}>
		<PanelHeader>Конкурс на активность</PanelHeader>
		{fetchedUser &&
		<Group title="Общая информация">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200} size={80}/> : null}
				description={"Участник конкурса"}>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}
		<Group title="Количество баллов">	
			<Cell before={<Icon24Like />} indicator={user.p_likes ? <Counter type="primary">{`${user.p_likes}`}</Counter> : null}>Лайки</Cell>
			<Cell before={<Icon24Like />} indicator={user.p_flikes ? <Counter type="primary">{`${user.p_flikes}`}</Counter> : null}>Лайки первым</Cell>
			<Cell before={<Icon24Like />} indicator={user.p_tlikes ? <Counter type="primary">{`${user.p_tlikes}`}</Counter> : null}>Лайки в числе первых</Cell>
			<Cell before={<Icon24Like />} indicator={user.p_alllikes ? <Counter type="primary">{`${user.p_alllikes}`}</Counter> : null}>Лайки всех постов</Cell>
			<Cell before={<Icon24Like />} indicator={user.p_gposts ? <Counter type="primary">{`${user.p_gposts}`}</Counter> : null}>Хорошие посты</Cell>
			<Cell before={<Icon24Like />} indicator={user.p_gcomm ? <Counter type="primary">{`${user.p_gcomm}`}</Counter> : null}>Хорошие комментарии</Cell>
		</Group>
	</Panel>
);

export default Home;
